/* MAP */
@import "application/style";


@media (min-width: 1200px) {
	.container {
	    width: 1024px;
	}
}

.map-view{ 
	width: 100%;
	min-height: 200px;
	height: 100%;
}

.map-search{
	padding: 15px;
	min-height: 30px;
	bottom: 50px;
	background: white;
	border-radius: 2px;
	position: absolute;
	margin-top: -50px;
	position : relative;
	z-index: 2;

}


/** Hero **/

.home{
	.header{
		z-index: 2;
		padding: 30px 0;
	}
	.hero-subheader{
		margin-top: -120px;
		height: 500px;
		.hero-subheader__content {
			.outer {
				.middle{
					.inner h2{
					    font-family: Montserrat;
					    color: white;
					    margin: auto;
					    max-width: 480px;
					    line-height: 45px;
						font-size: 30px;
					}
				}
			}
		}
	}
	
	.hero-overlay{
		position: absolute;
		top: 0; bottom:0; left:0; right:0;
		background: rgba(0, 0, 0, 0.75)
	}
}

.icon {
    font-size: 50px;
}